import { Component,HostListener } from '@angular/core';
import * as Datastore from 'nedb';
import { DataService } from './data.service';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import {Router} from "@angular/router";

@Component({
  selector: 'app-root',
  // host: { '(window:keydown)': 'doSomething($event)' },
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  db: any;
  records: any = [];
  @HostListener('document:keypress', ['$event'])
  handleKeyboardEvent(event: KeyboardEvent) {
    console.log(event)
    if (event.code === "Enter") {
      console.log("app")
      this.router.navigate(['/bill']); 
    }
  }
  // doSomething(event) {

  //   console.log("event")
  //   console.log(event)
  // }
  myGroup = new FormGroup({
    product: new FormControl(),
    stock: new FormControl()

  });
  constructor(
    private ds: DataService,
    private fb: FormBuilder,
    private router:Router
  ) {
    var filepath = __dirname + '/' + "product";
    this.db = new Datastore({ filename: filepath });
    this.db.loadDatabase(function (err) {

    });
    this.OnInit()
  }
  OnInit() {
    this.ds.getData()
      .then((docs) => {
        console.log(docs)
        this.records = docs;
      })
      .catch((err) => console.error(err));
  }

  addProduct() {
    console.log(this.myGroup)
    this.ds.addRecord(this.myGroup.value)
      .then((docs) => {
        console.log(docs)
        this.myGroup.value.product = "";
        this.myGroup.value.stock = "";
        this.OnInit()
      })
      .catch((err) => console.error(err));
  }
  delete(record) {
    console.log(record)
    this.ds.deleteRecord(record)
      .then((docs) => {
        console.log(docs)
        this.OnInit()
      })
      .catch((err) => console.error(err));
  }
}



